.lookup-input-disabled {
  input {
    visibility: hidden;
  }
  .sapMInputBaseIconContainer {
    opacity: 0.5;
    pointer-events: none;
  }
}
.o3-lookup-input {
  display: inline-flex;
  .sapMInputDescriptionWrapper {
    margin-left: 5px;
    box-sizing: content-box;
    background-color: #f5faff;
    border: 1px rgba(10,110,209,.3) solid;
    border-radius: 0.125rem;
    color: #32363a;
  }
}
