.navPopover {
  .navPopover-header {
    padding: 1rem 1rem .5rem;
    .sapMQuickViewPage .sapUiHLayout {
      margin-bottom: 0;
    }
     
  }
  .navPopover-title {
    font-size: 1.25rem;
  }
  .navPopover-separator {
    @include mx(1rem);
    border-bottom: 1px solid rgba(63,81,97,0.2);
  }
  .navPopover-link-group,
  .navPopover-button {
    padding: 0.5rem 1rem;
  }
  .navPopover-link-item {
    @include py(0.375rem);
    .sapMFlexItem {
      display: flex;
    }
  }
}

html.sap-phone {
  .navPopover-header {
    .sapMQuickViewPage .sapUiHLayout {
      margin: 0;
    }
  }
}