@mixin clearfix() {
  &::after {
    display: block;
    clear: both;
    content: "";
  }
}
@mixin px($num) {
  padding-left: $num;
  padding-right: $num;
}
@mixin py($num) {
  padding-top: $num;
  padding-bottom: $num;
}
@mixin mx($num) {
  margin-left: $num;
  margin-right: $num;
}
@mixin my($num) {
  margin-top: $num;
  margin-bottom: $num;
}
@mixin size($num) {
  width: $num;
  height: $num;
}
@mixin maxSize($num) {
  max-width: $num;
  max-height: $num;
}
@mixin minSize($num) {
  min-width: $num;
  min-height: $num;
}