.o3-cms {
  font-size: 14px;
  & > div {
    background-color: #fff;
  }
  .sapMListItems > li {
    position: relative;
    border-bottom: none;
    @include mx(36px);
    background-color: transparent !important;
    transition: box-shadow 0.2s ease;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: 2;
      display: block;
      transition: background-color 0.2s ease;
    }
    &:hover, &.hover, &.sapMLIBSelected {
      z-index: 1;
      box-shadow: 0 0 0 4px rgba(0,123,255,.25);
    }
    &.sapMLIBSelected:after {
      background-color: rgba(0,123,255,.1);
    }
    &.sapMLIBFocusable:focus {
      outline: none;
    }
  }
  .cms-item-richText {
    font-size: 100%;
    line-height: 2.12;
    p:first-child {
      margin-top: 0;
    }
    p:last-child {
      margin-bottom: 0;
    }
    a {
      color: inherit;
    }
    .table {
      width: 100%;
      margin-top: 1.5rem;
      margin-bottom: 1.5rem;
      td, th {
        padding: .75rem;
        vertical-align: top;
        border-top: 1px solid #dee2e6;
      }
    }
  }
  .cms-item-image {
    img {
      display: block;
      width: 100%;
    }
  }
  .cms-item-swiper {
    &:after,
    .sapMCrsl {
      pointer-events: none;
    }
    .sapMCrsl {
      border: none;
      background-color: transparent;
    }
    .sapMCrslPrev,
    .sapMCrslNext {
      pointer-events: all;
    }
    .sapMCrslControls {
      border: none;
    }
  }
}