
//手机版
.sapUiMedia-Std-Phone {
  .O3GridSearchHeader {
    & > .sapFDynamicPageHeaderContent {
      display: flex;
      flex-direction: column;
      .O3GridSearchForm-Toolbar {
        order: -1;
        margin-bottom: 1rem;
      }
    }
  }
}


.O3GridSearchForm {
  .sapUiLayoutCSSGridBoxLayoutContainer {
    gap: 1rem 1rem !important;
    grid-template-columns: repeat(auto-fill, minmax(14rem, 1fr)) !important;
  }
  .sapMLIB.sapUiLayoutCSSGridItem {
    border: none;
    outline: none;
    box-shadow: none;
  }
  & + .O3GridSearchForm-Toolbar {
    padding-left: 0;
    padding-right: 0;
  }
}
