.O3Launchpad {
  background-image: linear-gradient(to bottom,#dfe3e4,#f3f4f5);
  .sapUxAPObjectPageNavigation {
    box-shadow: inset 0 -0.0625rem #d9d9d9, 0 0 0.25rem 0 rgba(0,0,0,0.15)
  }
  .sapMScrollBarOuterDiv {
    display: none;
  }
  .sapUxAPObjectPageContainer,
  .sapUxAPObjectPageContainer + div,
  .sapUxAPObjectPageHeaderDetailsHidden {
    background-color: transparent;
  }
  .sapUxAPObjectPageSection:focus,
  .sapUxAPObjectPageSubSection:focus {
    outline: none;
  }
  .sapUiRespGrid.sapUiRespGridHSpace1 {
    padding: 0;
  }
  @at-root {
    .sapUiSizeCompact & {
      .sapUxAPObjectPageSectionHeader,
      .sapUxAPObjectPageSectionTitle {
        border-bottom: none;
      }
      .sapUxAPObjectPageSectionHeader {
        padding-top: .75rem;
      }
      .sapUxAPObjectPageSectionTitle {
        padding-bottom: 0;
      }
    }
    .sapUiSizeCompact &.sapUxAPObjectPageLayout-Std-Desktop {
      .sapUxAPBlockContainer {
        padding-top: .5rem;
      }
    }
    .sapUiSizeCompact &.sapUxAPObjectPageLayout-Std-Phone {
      .sapUxAPBlockContainer {
        padding-top: 0;
      }
      .sapUxAPBlockContainer {
        padding-left: 0;
      }
    }
    html.sap-desktop & {
      .sapMBtn:focus>.sapMFocusable {
        outline: none;
      }
    }
  }
  
  
}