.O3ValueHelpDialog {
  .sapMDialogScroll {
    display: flex;
    flex-direction: column;
    min-height: 100%;
    .sapMDialogScrollCont {
      display: flex;
      flex-grow: 1;
      min-height: 100%;
    }
  }
  .O3ValueHelpDialog-Content {
    justify-content: space-between;
    width: 100%;
  }
  .O3ValueHelpDialog-IconTabbar {
    display: flex;
    flex-flow: column;
    height: 100%;
    .sapMITBContainerContent {
      flex-grow: 1;
      overflow: auto;
      .sapMITBContent {
        height: 100%;
        min-height: 100%;
        box-sizing: border-box;
      }
    }
  }
  .O3ValueHelpDialog-FilterBar {
    padding: 0 1rem;
    .sapMTBShrinkItem {
      max-width: 450px;
    }
  }
  .O3ValueHelpDialog-MainLayout {
    height: 100%;
    // flex-grow: 1;
    // padding: 1rem 2rem;
  }
  .O3ValueHelpDialog-TokenizeGrid {
    padding: .5rem 1rem;
    background-color: #efefef;
    .sapMPanelExpandableIcon {
      transition: none;
    }
    .O3VHTokenizerHLayout {
      width: 100%;
      & > .sapUiHLayoutChildWrapper:first-child {
        width: calc(100% - 32px);
      }
    }
    .sapMPanel .sapMPanelHdr {
      font-size: 0.875rem;
    }
    .sapMPanelWrappingDiv {
      border: none;
    }
    .sapMPanelContent {
      padding: 0;
      margin-left: 2.75rem;
    }
    .sapMTokenizer {
      width: 100%;
      padding-top: 2px;
      min-height: 30px;
      border: #89919a 1px solid;
      border-radius: 5px;
    }
    .O3VHSelectedItemsText {
      height: 2.75rem;
      line-height: 2.75rem;
    }
  }
}

//手机版
.sapUiMedia-Std-Phone {
  .O3ValueHelpDialog {
    .O3ValueHelpDialog-TokenizeGrid {
      padding: 0;
      .O3VHTokenizerHLayout {
        margin-bottom: 0.5rem;
      }
    }
  }
}