
.sapUxAPObjectPageHeaderContent .sapUxAPObjectPageHeaderContentItem {
  &.O3HeaderContentItemFirst {
    @media (min-width: 768px) {
      float: right;
      margin-left: 2rem;
      margin-right: 0;
      text-align: right;
      .O3HeaderField .sapMLabel:after {
        margin-right: 0;
      }
      &.flexRow {
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        .O3HeaderField {
          margin-top: 0;
          margin-left: 2rem;
        }
      }
    }
  }
}

.O3HeaderField {
  min-width: 3rem;
  line-height: 1.32rem;
  .sapMLabel:after {
    content: ":";
    margin-right: .3rem;
  }
  .sapMText {
    line-height: 1.32rem;
  }
}
.sapUxAPObjectPageHeaderContentItem {
  .O3HeaderField {
    .sapMText {
      line-height: 1.32rem;
    }
    &.sapMVBox .sapMFlexItem {
      .sapMText,
      .sapMLnk,
      .sapMObjStatusText {
        font-size: 1.375rem;
      }
    }
    &.sapMVBox + .O3HeaderField {
      margin-top: .1rem;
    }
    &.sapMVBox + .sapMVBox{
      margin-top: .8rem;
    }
  }
}